.page-navigation {
  /* Reset any inherited styles */
  all: initial;
  display: block;
  font-family: 'MS Sans Serif', 'Geneva', sans-serif !important;
  background: linear-gradient(135deg, #6b46c1 0%, #4338ca 50%, #1e40af 100%);
  border: 3px ridge #c0c0c0;
  border-radius: 0;
  padding: 12px 8px 8px;
  margin-bottom: 20px;
  box-shadow:
    inset -1px -1px #0a0a0a,
    inset 1px 1px #dfdfdf,
    2px 2px 5px rgba(0, 0, 0, 0.3);
}

.nav-container {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  gap: 8px !important;
  flex-wrap: wrap !important;
  background: linear-gradient(180deg, #f0f0f0 0%, #d0d0d0 100%) !important;
  border: 2px inset #c0c0c0 !important;
  padding: 8px 12px !important;
  margin-bottom: 8px !important;
  font-family: 'MS Sans Serif', 'Geneva', sans-serif !important;
}

.nav-item {
  background: linear-gradient(180deg, #ffffff 0%, #e0e0e0 100%) !important;
  border: 2px outset #c0c0c0 !important;
  padding: 6px 14px !important;
  font-family: 'MS Sans Serif', 'Geneva', sans-serif !important;
  font-size: 13px !important;
  font-weight: bold !important;
  color: #000080 !important;
  cursor: pointer !important;
  text-decoration: none !important;
  display: inline-flex !important;
  align-items: center !important;
  gap: 6px !important;
  transition: all 0.1s !important;
  text-shadow: 1px 1px 0px #ffffff !important;
  line-height: normal !important;
}

.nav-item:hover {
  background: linear-gradient(180deg, #ffffcc 0%, #ffee88 100%);
  border: 2px outset #ffcc00;
  transform: translateY(-1px);
}

.nav-item:active {
  border: 2px inset #c0c0c0;
  background: linear-gradient(180deg, #e0e0e0 0%, #d0d0d0 100%);
  transform: translateY(0);
}

.nav-item.active {
  background: linear-gradient(135deg, #ffff00 0%, #ff9900 100%);
  border: 2px inset #ff6600;
  color: #660000;
  text-shadow: 1px 1px 0px #ffff99;
  animation: pulse-active 2s ease-in-out infinite;
}

@keyframes pulse-active {
  0%,
  100% {
    box-shadow: 0 0 5px #ff9900;
  }
  50% {
    box-shadow:
      0 0 15px #ff9900,
      0 0 25px #ffff00;
  }
}

.nav-icon {
  font-size: 16px;
  display: inline-block;
  animation: icon-bounce 3s ease-in-out infinite;
}

@keyframes icon-bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-2px);
  }
}

.nav-label {
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.nav-separator {
  color: #4444ff;
  font-weight: bold;
  font-size: 16px;
  margin: 0 4px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.nav-decoration {
  text-align: center;
  font-size: 11px;
  color: #ffff00;
  text-shadow: 1px 1px 2px #000000;
  font-family: 'Courier New', monospace;
  letter-spacing: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.nav-status {
  animation: glow 2s ease-in-out infinite;
}

@keyframes glow {
  0%,
  100% {
    text-shadow:
      1px 1px 2px #000000,
      0 0 5px #ffff00;
  }
  50% {
    text-shadow:
      1px 1px 2px #000000,
      0 0 10px #ffff00,
      0 0 15px #ff9900;
  }
}

.blink-text {
  animation: blink 1s step-start infinite;
  font-size: 14px;
}

@keyframes blink {
  0%,
  50% {
    opacity: 1;
  }
  51%,
  100% {
    opacity: 0;
  }
}

/* Mobile responsiveness */
@media (max-width: 640px) {
  .nav-container {
    gap: 4px;
    padding: 6px;
  }

  .nav-item {
    font-size: 11px;
    padding: 4px 8px;
  }

  .nav-icon {
    font-size: 14px;
  }

  .nav-label {
    display: none;
  }

  .nav-separator {
    margin: 0 2px;
  }

  .nav-decoration {
    font-size: 9px;
    letter-spacing: 1px;
  }
}
