/* Contact page background - paper texture */
.browser-viewport div#contact {
  background: linear-gradient(180deg, #fff9e6 0%, #e6f0ff 100%);
  position: relative;
}

.browser-viewport div#contact::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image:
    repeating-linear-gradient(
      90deg,
      transparent,
      transparent 50px,
      rgba(200, 180, 150, 0.25) 50px,
      rgba(200, 180, 150, 0.25) 51px
    ),
    repeating-linear-gradient(
      0deg,
      transparent,
      transparent 50px,
      rgba(150, 180, 200, 0.2) 50px,
      rgba(150, 180, 200, 0.2) 51px
    );
  pointer-events: none;
}

#contact p {
  margin: 20px 0;
}
#contact input,
#contact textarea {
  border: 2px solid #666;
  font-family: 'VG';
}
#contact button.ui.button.blue {
  font-family: 'VG';
  width: 100%;
  margin-bottom: 10px;
}
#contact .ui.positive.message {
  background-color: #21ba45;
}
#contact .ui.negative.message {
  background-color: #db2828;
}
#contact .ui.message .header {
  color: white;
  font-family: 'VG';
}
div#sending-mail {
  height: 100px;
  text-align: center;
  font-size: 40px;
  padding: 20px;
  margin: 40px;
}
i.spinner.icon {
  animation: sending 1s linear infinite;
  height: 80px;
  width: 80px;
  padding-top: 30px;
}
@keyframes sending {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media screen and (max-width: 768px) {
  #contact input,
  #contact textarea {
    font-size: 12px;
  }
}

/* Retro Guestbook Style */
.browser-viewport div#contact {
  background:
    repeating-linear-gradient(0deg, #ffffff, #ffffff 20px, #e8e8ff 20px, #e8e8ff 21px),
    linear-gradient(to right, #ff9999 0px, #ff9999 2px, transparent 2px);
  min-height: 100%;
  font-family: 'Comic Sans MS', 'Marker Felt', cursive;
  position: relative;
}

.browser-viewport div#contact .page-content h1 {
  text-align: center;
  color: #ff00ff;
  font-size: 28px;
  text-shadow: 2px 2px 0px #ffff00;
  text-decoration: underline wavy;
  margin-bottom: 20px;
  transform: rotate(-2deg);
  animation: wobble 3s ease-in-out infinite;
}

@keyframes wobble {
  0%,
  100% {
    transform: rotate(-2deg);
  }
  50% {
    transform: rotate(2deg);
  }
}

/* Sign my guestbook header */
.guestbook-header {
  text-align: center;
  margin: 20px 0;
  position: relative;
}

.guestbook-header::before {
  content: '✏️';
  font-size: 32px;
  animation: write 2s ease-in-out infinite;
  display: inline-block;
  margin-right: 10px;
}

@keyframes write {
  0%,
  100% {
    transform: translateY(0) rotate(0deg);
  }
  25% {
    transform: translateY(-5px) rotate(-10deg);
  }
  75% {
    transform: translateY(-5px) rotate(10deg);
  }
}

/* Visitor counter for guestbook */
.guestbook-counter {
  background: #000000;
  color: #00ff00;
  padding: 5px 10px;
  font-family: 'Courier New', monospace;
  font-size: 10px;
  text-align: center;
  margin: 10px auto;
  width: fit-content;
  border: 2px solid #00ff00;
}

/* Form styling like handwritten */
.browser-viewport div#contact form {
  margin: 20px 0;
}

.browser-viewport div#contact .page-content input,
.browser-viewport div#contact .page-content textarea {
  width: 100%;
  background: transparent;
  border: none;
  border-bottom: 1px dotted #0000ff;
  font-family: 'Comic Sans MS', cursive;
  font-size: 14px;
  color: #0000ff;
  padding: 5px;
  margin: 10px 0;
}

.browser-viewport div#contact .page-content input:focus,
.browser-viewport div#contact .page-content textarea:focus {
  outline: none;
  border-bottom: 2px solid #ff00ff;
  background: rgba(255, 255, 0, 0.1);
}

.browser-viewport div#contact textarea {
  min-height: 100px;
  resize: vertical;
  border: 1px dotted #0000ff;
}

/* Required field asterisk */
.required-field {
  color: #ff0000;
  font-size: 16px;
  font-weight: bold;
}

/* Fake CAPTCHA */
.fake-captcha {
  background: #f0f0f0;
  border: 2px solid #808080;
  padding: 10px;
  margin: 15px 0;
  text-align: center;
}

.captcha-text {
  background: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 2px,
    rgba(0, 0, 0, 0.1) 2px,
    rgba(0, 0, 0, 0.1) 4px
  );
  font-family: 'Courier New', monospace;
  font-size: 20px;
  letter-spacing: 5px;
  color: #333333;
  padding: 5px;
  margin: 10px 0;
  user-select: none;
  transform: skewX(-10deg);
}

.captcha-input {
  width: 150px;
  margin: 0 auto;
}

/* Submit button styled as pen */
.browser-viewport div#contact .page-content button {
  background: linear-gradient(135deg, #ff69b4, #ff1493);
  color: #ffffff;
  border: 2px solid #000000;
  padding: 10px 30px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transform: rotate(-5deg);
  transition: all 0.3s;
  box-shadow: 3px 3px 0px #000000;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.browser-viewport div#contact .page-content button:hover {
  transform: rotate(0deg) scale(1.1);
  box-shadow: 5px 5px 0px #000000;
  background: linear-gradient(135deg, #ff1493, #ff69b4);
}

.browser-viewport div#contact .page-content button:active {
  transform: rotate(0deg) scale(0.95);
  box-shadow: 1px 1px 0px #000000;
}

/* Success message styled as thank you note */
.browser-viewport div#contact .ui.positive.message {
  background: linear-gradient(135deg, #90ee90, #98fb98);
  border: 3px double #008000;
  color: #004000;
  padding: 20px;
  text-align: center;
  font-size: 16px;
  transform: rotate(2deg);
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
}

.browser-viewport div#contact .ui.positive.message .header {
  color: #004000;
  font-size: 20px;
  text-decoration: underline;
}

/* Error message */
.browser-viewport div#contact .ui.negative.message {
  background: repeating-linear-gradient(45deg, #ffcccc, #ffcccc 10px, #ff9999 10px, #ff9999 20px);
  border: 3px solid #cc0000;
  color: #660000;
  padding: 15px;
  font-weight: bold;
}

/* Sending animation */
.browser-viewport div#sending-mail {
  background: url('data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==');
  text-align: center;
}

.browser-viewport div#sending-mail::before {
  content: '📮 Sending your message... 📮';
  display: block;
  font-size: 16px;
  color: #0066cc;
  margin-bottom: 20px;
  animation: flash 0.5s ease-in-out infinite;
}

@keyframes flash {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
}

/* Previous entries decoration */
.previous-entries {
  margin-top: 30px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.5);
  border: 1px dashed #cc00cc;
}

.entry-signature {
  font-family: 'Brush Script MT', cursive;
  font-size: 18px;
  color: #660099;
  transform: rotate(-3deg);
  display: inline-block;
  margin: 10px;
}

/* Decorative elements */
.sparkle {
  display: inline-block;
  animation: sparkle 1.5s ease-in-out infinite;
}

@keyframes sparkle {
  0%,
  100% {
    transform: scale(1) rotate(0deg);
  }
  50% {
    transform: scale(1.2) rotate(180deg);
  }
}

.browser-viewport div#contact .page-content p {
  color: #333333;
  font-size: 12px;
  line-height: 20px;
  margin: 10px 0;
}
