/* Shared Retro Theme Variables and Styles */
:root {
  --retro-primary: #0000ff;
  --retro-secondary: #ff00ff;
  --retro-accent: #ffff00;
  --retro-success: #00ff00;
  --retro-warning: #ff9900;
  --retro-danger: #ff0000;
  --retro-bg-light: #f0f0f0;
  --retro-bg-dark: #c0c0c0;
  --retro-text: #000080;
  --retro-border: #808080;
}

/* Common Page Container */
.retro-page {
  background: linear-gradient(180deg, #ffffff 0%, #f0f0f0 100%);
  padding: 20px;
  min-height: 400px;
}

.retro-page h1 {
  text-align: center;
  color: var(--retro-primary);
  font-family: 'Comic Sans MS', 'Arial', sans-serif;
  font-size: 24px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
}

.retro-page h2 {
  color: var(--retro-text);
  font-family: 'Arial', sans-serif;
  font-size: 18px;
  margin: 15px 0;
}

.retro-page p {
  font-family: 'Times New Roman', serif;
  font-size: 14px;
  line-height: 1.6;
  color: #333333;
  margin: 10px 0;
}

/* Retro Box Style */
.retro-box {
  background: var(--retro-bg-light);
  border: 2px ridge var(--retro-border);
  padding: 15px;
  margin: 15px 0;
  box-shadow:
    inset -1px -1px #000000,
    inset 1px 1px #ffffff;
}

/* Retro Button Style */
.retro-button {
  background: linear-gradient(180deg, #ffffff 0%, #e0e0e0 100%);
  border: 2px outset #c0c0c0;
  padding: 8px 16px;
  font-family: 'MS Sans Serif', 'Geneva', sans-serif;
  font-size: 13px;
  font-weight: bold;
  color: var(--retro-text);
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  margin: 5px;
  transition: all 0.1s;
}

.retro-button:hover {
  background: linear-gradient(180deg, #f0f0f0 0%, #d0d0d0 100%);
  transform: translateY(-1px);
}

.retro-button:active {
  border: 2px inset #c0c0c0;
  transform: translateY(0);
}

.retro-button.primary {
  background: linear-gradient(180deg, #4444ff 0%, #0000cc 100%);
  color: white;
  border-color: #0000aa;
}

/* Visitor Counter Style */
.visitor-counter {
  display: inline-block;
  background: #000000;
  color: #00ff00;
  font-family: 'Courier New', monospace;
  font-size: 14px;
  padding: 4px 8px;
  border: 2px inset #808080;
  margin: 10px;
}

/* Badge Styles */
.retro-badge {
  display: inline-block;
  background: linear-gradient(45deg, var(--retro-danger), var(--retro-warning));
  color: white;
  font-weight: bold;
  font-size: 11px;
  padding: 2px 6px;
  margin: 0 3px;
  border: 1px solid black;
  border-radius: 3px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  animation: badge-pulse 2s ease-in-out infinite;
}

.retro-badge.new {
  background: linear-gradient(45deg, #00ff00, #00cc00);
}

.retro-badge.hot {
  background: linear-gradient(45deg, #ff0000, #ff6600);
}

.retro-badge.cool {
  background: linear-gradient(45deg, #0099ff, #0066cc);
}

@keyframes badge-pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

/* Content Section Wrapper */
.content-section {
  margin-bottom: 25px;
}

.content-section-title {
  background: linear-gradient(90deg, var(--retro-primary) 0%, var(--retro-secondary) 100%);
  color: white;
  padding: 8px 15px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: 2px outset #808080;
  margin-bottom: 15px;
}

/* Decorative Elements */
.sparkle {
  display: inline-block;
  animation: sparkle 1.5s ease-in-out infinite;
}

@keyframes sparkle {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(1.2);
  }
}

.rainbow-text {
  animation: rainbow-text 3s linear infinite;
  font-weight: bold;
}

@keyframes rainbow-text {
  0% {
    color: #ff0000;
  }
  16% {
    color: #ff8800;
  }
  33% {
    color: #ffff00;
  }
  50% {
    color: #00ff00;
  }
  66% {
    color: #0088ff;
  }
  83% {
    color: #8800ff;
  }
  100% {
    color: #ff0000;
  }
}
